:root {
  --base-font-size: 16px;

  --color-white: #ffffff;
  --color-black: #120c0b;

  --max-width: 1200px;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  color: var(--color-white);
  background-color: var(--color-black);
}

body {
  font-size: var(--base-font-size);
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-size: 1.5em;
  font-weight: 700;
  margin-bottom: 1em;
}

h2 {
  font-size: 1.2em;
  margin-bottom: .8em;
}

p {
  margin: .5em 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid var(--color-white);
  padding: 0 2em;
}

.navbar {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1em;
}
.navbar--item {
  text-align: center;
}

.container {
  max-width: var(--max-width);
  margin: 3em auto;
}

.form-group {
  margin: .5em 0;
  width: 100%;
}

.form--input {
  background-color: var(--color-white);
  color: var(--color-black);
  width: 100%;
  height: 2em;
  line-height: 2em;
  padding-left: .4em;
  border: none;
  border-radius: 5px;
}

.form--button-primary {
  background-color: var(--color-white);
  color: var(--color-black);
  height: 2em;
  line-height: 2em;
  padding: 0 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}